* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header>div {
  font-size: 50px;
  padding-bottom: 20px;
}

.list {
  border: 1px solid lightgray;
  background-color: #eaeaea;
  padding: 10px 10px;
  margin-bottom: 10px;
}

.list .listname {
  /* border: 1px solid red; */
  /* background-color: #61dafb; */
  font-size: 30px;
  font-weight: bold;

}

.list .good {
  font-size: 20px;
}

.list .date {
  font-size: 20px;
}

@media (max-width: 800px) {
  .list {
    width: 80%;
  }

}